.cms-news{
    background-color: #000;
    color: #fff;
    
    .news-page{
        position: relative;
        padding-top: 200px;
        padding-bottom: 60px;
        @include mq('tablet', min) {
            padding-top: 300px;
            padding-bottom: 150px;
            padding-left: 0;
            padding-right: 0;
        }
        // > div{
        //     position: relative;
        //     z-index: 1;
        //     @include mq('tablet', min) {
        //         width: 100%;
        //     }
        // }
        .container{
            flex-direction: column;
            @include mq('desktop-wide') {
                padding: 0 60px;
            }
        }
        .content{
            // max-width: 100%;
            text-align: left;
            align-items: flex-start;
            @include mq('tablet') {
                // margin-bottom: 60px;
            }
            @include mq('tablet', min) {
                // margin-bottom: 100px;
            }
            .contain{
                @include mq('tablet') {
                    
                }
                @include mq('tablet', min) {
                    margin-left: 0;
                    padding: 0 40px;
                }
            }
        }
        .items{
            .item{
                display: flex;
                // flex-wrap: wrap;
                margin-top: 60px;
                @include mq('tablet') {
                    flex-wrap: wrap;
                    flex-direction: column;
                }
                @include mq('tablet', min) {
                    margin-top: 100px;
                }
                &:first-child{
                    margin-top: 0;
                }
                &:nth-child(even){
                    .news-content{
                        @include mq('tablet', min) {
                            order: 1;
                        }
                        .content{
                            .contain{
                                @include mq('tablet') {
                                    display: flex;
                                    flex-flow: wrap column;
                                    align-items: flex-end;
                                }
                            }
                            @include mq('tablet', min) {
                                padding-left: 0;
                            }
                        }
                    }
                }

            }
            .news-content{
                // width: 36%;
                flex-grow: 2;
            }
            .news-photo{
                // width: 64%;
            }
        }
    }

    .news-wrap{
        background-color: #fff;
        color: #000;
    }

    .news-slide{
        position: relative;
        img{
            display: block;
            width: 100%;
        }
        .caption{
            padding-top: 20px;
            @include mq('tablet', min) {
                padding-top: 30px;
            }
            h3.title{
                @include mq('tablet') {
                    font-size: 24px;
                }
            }
        }
        .swiper{
            @include mq('tablet') {
                overflow: visible;
                padding-right: 100px;
            }
        }
        .swiper-button{
            top: 26%;
            color: #000;
            @include mq('tablet') {
                display: none;
            }
            &.swiper-button-prev-custom{
                left: -60px;
            }
            &.swiper-button-next-custom{
                right: -60px;
            }
        }
    }
    a.more{
        color: #fff;
        &:before,
        &:after{
            background-color: rgba($color: #fff, $alpha: 1);
        }
    }
}

.news-detail-page{
    position: relative;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    padding-top: 200px;
    padding-bottom: 60px;
    a{
        color: #fff;
    }
    @include mq('tablet', min) {
        padding-top: 300px;
        padding-bottom: 150px;
    }
    .container{
        padding: 0 60px;
        @include mq('tablet') {

        }
    }
    .heading-block{
        .container{
            align-items: flex-end;
            .content{
                flex-grow: 2;
                .contain{
                    display: flex;
                    flex-direction: column;
                    align-items: flex-end;
                    text-align: right;
                }
                .architect-name{
                    font-family: 'Montserrat', sans-serif;
                    font-size: 25px;
                    font-weight: 700;
                    margin-bottom: 6px;
                }
            }
        }
    }
    .news-photo{
        margin: 100px 0;
        position: relative;
        &:after{
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: rgba(0,0,0,.56);
            // background: rgb(0,0,0);
            // background: linear-gradient(0deg,rgba(0,0,0,.39),rgba(0,0,0,.8));
            transition: all .5s ease;
            // z-index: -1;
        }
        img{
            object-fit: cover;
            object-position: center;
            width: 100%;
            height: 650px;
            display: block;
        }
    }
    .gallery-detail{
        display: flex;
        // align-items: center;
        justify-content: space-evenly;
        margin: 50px 0;
        img{
            display: block;
        }
        img + img{
            // margin-left: 40px;
        }
    }
    .news-desscription{
        .container{
            flex-direction: column;
        }
    }
}