// Typography
$f-stack-sans: 'Roboto', sans-serif;
// $f-stack-serif: 'Roboto', serif;
// $f-stack-special: 'Roboto', sans-serif;
$f-stack-default: $f-stack-sans;

// Brand Color
$c-red: #a12b2b;

// Base Color
$c-text: #000;

// =============================================
// Custom Vars
// =============================================

// Color
$c-border: #cbcbcb;

// default border radius
$b-radius: 6px;

// mobile window padding
$m-window-padding: 15px;

@-webkit-keyframes rotating {
    from{
        -webkit-transform: rotate(0deg);
    }
    to{
        -webkit-transform: rotate(360deg);
    }
}

@keyframes zoominoutnotifi {
    0% {
        transform: scale(1);
    }
    50% {
        transform: scale(1.4);
    }
    100% {
        transform: scale(1);
    }
}