header{
	nav{
		> ul{
			display: flex;
			>li{
				margin-right: 60px;
				position: relative;
				@include mq('tablet', min) {
					margin-right: 30px;
				}
				@include mq(1336px, min) {
					margin-right: 60px;
				}
				&:before {
					content: '';
					position: absolute;
					bottom: 0;
					left: 0;
					width: 100%;
					height: 2px;
					background-color: $color-white;
					transform: scale3d(0, 1, 1);
					@include transition (all .3s);
					-webkit-transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
					transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
					-webkit-transition-duration: 0.3s;
					transition-duration: 0.3s;
					pointer-events: none;
				}

				&:after {
					content: '';
					position: absolute;
					top: 100%;
					left: 0;
					width: 100%;
					height: 40px;
					background: transparent;
					pointer-events: none;
				}
				&:hover{
					&:before{
						transform: scale3d(1, 1, 1);
					}
					> ul{
						opacity: 1;
					}
				}
				&.active{
					&:before{
						transform: scale3d(1, 1, 1);
					}
				}
				// @include mq('tablet', min) {
				// 	&.mega-menu{
				// 		> a{
				// 			&:after{
				// 				content: '';
				// 				border: solid transparent;
				// 				height: 0;
				// 				width: 0;
				// 				position: relative;
				// 				top: -1px;
				// 				pointer-events: none;
				// 				border-color: rgba(255, 255, 255, 0);
				// 				border-top-color: #fff;
				// 				border-width: 6px;
				// 				margin-left: 8px;
				// 			}
				// 		}
				// 	}
				// }
				> a{
					color: $color-white;
					padding:  5px 10px;
					display: flex;
					align-items: flex-end;
					@include mq('tablet', min) {
						padding:  5px 5px;
					}
					@include mq('desktop-wide', min) {
						padding:  5px 10px;
					}
				}
				ul{
					background-color: rgba($color: #000000, $alpha: .5);
					position: absolute;
					top: 33px;
					left: -6px;
					padding: 10px 0;
					min-width: 125px;
					opacity: 0;
					transition: opacity .5s ease;
					@include mq('tablet') {
						display: none;
					}
					li{

					}
					a{
						padding: 5px 15px;
						display: block;
						&:hover{
							text-decoration: underline;
						}
					}
				}
			}
		}
		@include mq('tablet') {
			position: fixed;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			z-index: 3;
			background-color: rgba(26,27,32,.98);
			opacity: 0;
			-webkit-transition: opacity .3s ease,visibility .3s step-end;
			transition: opacity .3s ease,visibility .3s step-end;
			pointer-events: none;
			visibility: hidden;
			> ul{
				flex-direction: column;
				position: absolute;
				top: calc(50% - 60px);
				left: 50%;
				-webkit-transform: translate(-50%,-50%);
				transform: translate(-50%,-50%);
				text-align: center;
				> li{
					display: block;
					font-size: 26px;
					float: none;
					margin: 0;
					margin-bottom: 20px;
					letter-spacing: .3px;
					margin-left: auto;
					margin-right: auto;
				}
			}
		}
		@include mq('tablet', min) {
			flex-grow: 2;
			margin: 0 90px;
		}
	}

	&.opened nav{
		@include mq('tablet') {
			transition: opacity .3s ease,visibility 0s step-start;
			visibility: visible;
			opacity: 1;
			pointer-events: all;
		}
	}

	&.fixed_white{
		nav > ul > li:before{
			background-color: #000;
		}
	}
}