header{
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	z-index: 11;
	padding: 0 20px;
	color: #fff;
	@include mq('tablet', min) {
		padding: 0 20px;
	}
    @include mq('desktop', min) {
        padding: 0 60px;
    }
    @include mq('desktop-wide', min) {
        padding: 0 130px;
    }
	.header-wrapper{
		
	}
	a{
		color: #fff;
	}
	&:before{
		content: "";
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height: 74px;
		background-color: #fafafa;
		border-bottom: 1px solid #f2f2f2;
		z-index: -1;
		opacity: 0;
		transition: opacity .5s ease;
		@include mq('tablet') {
			height: 70px;
		}
	}
	.header-wrapper{
		display: flex;
		align-items: center;
		position: relative;
		top: 14px;
		transition: top .3s ease;
		@include mq('tablet', min) {
			top: 50px;
		}
	}
	.hamburger{
		position: absolute;
		right: -4px;
		top: 0;
		padding: 8px 0;
		transform: scale(0.8);
		z-index: 10;
		@include mq('tablet', min) {
			display: none;
		}
	}
	&:not(.fixed_white){
		.hamburger{
			.hamburger-inner, 
			.hamburger-inner::before, 
			.hamburger-inner::after{
				background-color: #fff;
			}
			&.is-active{
				.hamburger-inner, 
				.hamburger-inner::before, 
				.hamburger-inner::after{
					background-color: #fff;
				}
			}
		}
	}
	&.fixed_white{
		color: $color-black;
		&:before{
			opacity: 1;
		}
		a{
			color: $color-black;
		}
		.header-wrapper{
			@include mq('tablet', min) {
				top: 14px;
			}
		}
		.logo{
			@include mq('tablet', min) {
				width: 60px;
				height: 48px;
			}
			.white{
				opacity: 0;
			}
			.black{
				opacity: 1;
			}
		}
		.lang{
			ul{
				>li{
					&.active,
					&.current-lang{
						background-color: #707070;
						color: #fff;
						a{
							color: #fff;
						}
					}
				}
			}
		}
	}

	.logo{
		width: 50px;
		transition: width .5s ease;
		position: relative;
		z-index: 10;
		flex-shrink: 0;
		@include mq('tablet', min) {
			// width: 90px;
			// height: 72px;
			width: 60px;
			height: 48px;
		}
		img{
			position: absolute;
			top: 0;
			left: 0;
		}
		.white{
			opacity: 1;
		}
		.black{
			opacity: 0;
		}
	}

	.lang{
		border: 1px solid #707070;
		height: 26px;
		border-radius: 20px;
		padding: 3px;
		@include mq('tablet') {
			position: absolute;
			top: 8px;
    		right: 60px;
			z-index: 10;
		}
		ul{
			display: flex;
			>li{
				width: 32px;
				height: 18px;
				flex-grow: 1;
				line-height: 18px;
				text-align: center;
				text-transform: capitalize;
				cursor: pointer;
				&.active,
				&.current-lang{
					background-color: #fff;
					border-radius: 10px;
					color: #000;
					pointer-events: none;
					a{
						color: #000;
					}
				}
			}
		}
	}
}

