.wow {
    visibility: hidden;
}

.page-main{
    .container{
        display: flex;
        flex-wrap: wrap;
        width: 100%;
        max-width: 1440px;
        // padding-left: 20px;
        // padding-right: 20px;
        margin: 0 auto;
        // @include mq('desktop-wide') {
        //     padding-left: 60px;
        //     padding-right: 60px;
        // }
        // @include mq('desktop-wide', min) {
        //     max-width: 1440px;
        // }
    }
}


.company-info{
    display: flex;
    padding: 0 20px;
    color: #fff;
    @include mq('tablet') {
        flex-direction: column;
    }
    @include mq('tablet-wide', min) {
        padding: 0 60px;
    }
    @include mq('desktop-wide', min) {
        padding: 0 130px;
    }
    .address{
        display: flex;
        @include mq('tablet-wide') {
            flex-direction: column;
        }
        @include mq('tablet-wide', min) {
            flex-grow: 2;
            justify-content: space-evenly;
            padding: 0 0 0 50px;
        }
        @include mq('desktop-wide', min) {
            padding: 0 100px 0 50px;
        }
    }
}

.social-block{
	display: flex;
	justify-content: space-between;
	width: 140px;
	@include mq('tablet-wide') {
		margin-top: 20px;
        order: 1;
	}
	a{
		color: #fff;
		transform: scale(1);
		transition: transform 0.3s ease 0s;
		display: block;
		&:hover{
			transform: scale(1.3);
		}
        i{
            font-size: 24px;
            line-height: 1;
        }
	}
}


a.more{
    position: relative;
    display: flex;
    align-items: flex-start;
    color: #000;
    font-size: 14px;
    margin-left: 1px;
    opacity: .75;
    transition: opacity .5s ease;
	span{
		// margin-right: 10px;
        padding: 0 9px;
	}
    &:before,
    &:after{
        content: "";
        display: block;
        position: relative;
        top: 10px;
        height: 1px;
        background-color: rgba($color: #000, $alpha: 1);
        transition: width .5s ease;
    }
    &:before{
        width: 8px;
    }
	&:after{
        width: 56px;
        // animation: rightLine 1s forwards;
	}
    &:hover{
        opacity: 1;
        &:before{
            width: 56px;
        }
        &:after{
            width: 8px;
        }
    }
}

.back-link{
    a{
        display: flex;
        align-items: center;
        font-size: 18px;
        &:before{
            content: "\e900";
            font-family: 'td-icon' !important;
            speak: never;
            font-style: normal;
            font-weight: normal;
            font-variant: normal;
            text-transform: none;
            line-height: 1;
            -webkit-font-smoothing: antialiased;
            -moz-osx-font-smoothing: grayscale;
            font-size: 24px;
        }
        span{
            margin-left: 10px;
        }
    }
}

.flashLight{
	position: fixed;
    pointer-events: none;
    top: -180px;
    left: -180px;
    display: block;
    width: 359px;
    height: 359px;
    background-color: #fff;
    border-radius: 50%;
    mix-blend-mode: overlay;
    -webkit-filter: blur(141px);
    filter: blur(141px);
    z-index: 100;
    -webkit-transform: matrix(1,0,0,1,-100,-100);
    transform: matrix(1,0,0,1,-100,-100);
    opacity: 0.8;
    -webkit-transition: opacity 1.2s ease;
    transition: opacity 1.2s ease;
}

.block{
    position: relative;
    padding: 40px 20px;
    font-size: 16px;
    @include mq('tablet', min) {
        padding: 100px 20px;
    }
    @include mq('desktop', min) {
        padding: 120px 60px;
    }
    @include mq('desktop-wide', min) {
        font-size: 18px;
        padding: 150px 130px;
    }
    img{
        display: block;
    }
    .content{
        display: flex;
        align-items: center;     
        @include mq('tablet') {
            margin-bottom: 25px;
        }   
        .contain{
            @include mq('tablet', min) {
                max-width: 550px;
                padding-left: 40px;
                padding-right: 40px;
            }
            @include mq('desktop-wide', min) {
                padding-left: 125px;
            }
        }
        p{
            text-align: justify;
        }
        &.ltr{
            justify-content: end;
            text-align: right;
            .contain{
                @include mq('tablet', min) {
                    padding-left: 0;
                    // margin-right: 15%;
                }
            }
        }
    }
}