.banner-main{
    position: relative;
    background-color: #000;
    height: 100vh;
    height: --webkit-fill-available;
    overflow: hidden;
    // @include mq('tablet') {
    //     height: calc(100vh - 60px);
    // }
    .company-info{
        position: absolute;
        bottom: 40px;
        z-index: 10;
        left: 0;
        right: 0;
        @include mq('tablet') {
            bottom: 60px;
        }
    }
    .swiper-slide{
        position: relative;
        &:after{
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: rgba(0,0,0,.56);
            // background: rgb(0,0,0);
            // background: linear-gradient(0deg,rgba(0,0,0,.39),rgba(0,0,0,.8));
            transition: all .5s ease;
            z-index: -1;
        }
        img {
            height: 100vh;
            width: 100%;
            object-fit: cover;
            position: relative;
            z-index: -1;
        }   
        .img {
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            background-size: cover;
            background-position: center  center;
            background-repeat: no-repeat;
            transition: 3s all;
            transform: scale(1.2);
        }
        
        .swiper-slide-active .img {
                animation: scale 3s ease forwards;
        }
        
        @keyframes scale {
            0% {
                transform: scale(1);
            }
        
            100% {
                 transform: scale(1.2);
            }
        }
        .caption{
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            color: #fff;
            z-index: 1;
            @include mq('tablet') {
                left: 50px;
                right: 50px;
            }
            @include mq('tablet', min) {
                left: 150px;
                max-width: 500px;
            }
            @include mq('tablet-wide', min) {
                left: 250px;
                max-width: 800px;
            }
            @include mq('desktop-wide', min) {
                left: 310px;
            }
            h2{
                margin-bottom: 10px;
                // min-height: 116px;
                transition-duration: 1s;
                transition-timing-function: ease;
                font-family: 'Montserrat', sans-serif;
                font-style: normal;
                font-size: 34px;
                font-weight: 700;
                line-height: normal;
                text-transform: uppercase;
                @include mq('tablet') {
                    font-size: 34px;
                }
                @include mq('tablet', min) {
                    font-size: 40px;
                }
                @include mq('tablet-wide', min) {
                    font-size: 46px;
                }
                @include mq('desktop-wide', min) {
                    font-size: 56px;
                }
                > a{
                    color: #fff;
                }
            }
            .more{
                // font-family: Montserrat;
                font-style: normal;
                font-weight: 400;
                line-height: 1.3;
                font-size: 16px;
                letter-spacing: .12em;
                // text-transform: uppercase;
                text-align: center;
                color: #fff;
                @include mq('tablet') {
                    font-size: 14px;
                }
                &:before,
                &:after{
                    background-color: rgba($color: #fff, $alpha: .8);
                }
            }
        } 
    }
    .swiper-button{
        color: #fff;
    }
}

.poster__slide{
	position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    width: calc(100% - 222px);
    height: 100%;
    background-size: cover;
    background-position: 50%;
    -webkit-transform: translate(100%);
    transform: translate(100%);
    -webkit-transition: opacity 1s ease;
    transition: opacity 1s ease;
    z-index: 1;
    pointer-events: none;

	-webkit-transform: translate(100%);
    transform: translate(100%);
    opacity: 1;
    pointer-events: all;
    -webkit-transition: opacity 1.5s ease;
    transition: opacity 1.5s ease;
    -webkit-animation: fadeIn-data-v-133d00b4 1s forwards;
    animation: fadeIn-data-v-133d00b4 1s forwards;
	@include mq('tablet') {
		display: none;
	}
	.slide__container{
		position: absolute;
		width: 100%;
		-webkit-transform-origin: left center;
		transform-origin: left center;
		-webkit-transform: rotate(-90deg);
		transform: rotate(-90deg);
		top: auto;
		left: 116px;
		bottom: -12px;
		max-width: 328px;
		max-height: 114px;
		overflow: hidden;
	}
	.slide__title{
		cursor: pointer;
		font-family: 'montserratbold', sans-serif;
		color: #fff;
		letter-spacing: 0;
		font-size: 48px;
		line-height: 48px;
		color: transparent;
		-webkit-text-stroke: 1px #fff;
		text-stroke: 1px #fff;
		text-shadow: none;
		transition: color .5s ease;
		&:hover{
			color: #fff;
		}
	}
	.slide__label{
		font-weight: 400;
		line-height: normal;
		font-size: 14px;
		// letter-spacing: .06em;
		color: #fff;
	}
}

