.swiper-button{
    width: 50px;
    height: 50px;
    position: absolute;
    top: 50%;
    z-index: 10;
    transform: translateY(-50%) scale(1);
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
	transition: transform 0.3s ease 0s;
    &:hover{
		&:hover{
			transform: translateY(-50%) scale(1.3);
		}
    }
    &:after{
        font-family: 'td-icon' !important;
        speak: never;
        font-style: normal;
        font-weight: normal;
        font-variant: normal;
        text-transform: none;
        line-height: 1;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        font-size: 30px;
    }
    &.swiper-button-prev-custom{
        left: 20px;
        @include mq('tablet') {
            left: 0;
        }
        &:after{
            content: "\e900";
        }
    }
    &.swiper-button-next-custom{
        right: 20px;
        @include mq('tablet') {
            right: 0;
        }
        &:after{
            content: "\e901";
        }
    }
    &.swiper-button-disabled{
        opacity: .5;
        pointer-events: none;
    }
}