form {
    margin-bottom: 0;
}

fieldset,
figure {
    padding: 0;
    margin: 0;
    border: none;
}

input,
optgroup,
select,
textarea {
    
}

input:-internal-autofill-selected{
    background: none;
}

input {
    appearance: none;
}
select {
    appearance: none;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}
input[type="number"] {
    -moz-appearance: textfield !important;
}
select::-ms-expand {
    display: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
input[type="password"] {
    width: 100%;
    padding: 20px 0 6px;
    color: #fff;
    font-size: 16px;
    border-radius: 0;
    box-shadow: none;
    border: none;
    background: transparent;
    border-bottom: 1px solid #fff;
    appearance: none;
    -webkit-rtl-ordering: logical;
}

textarea.input-text{
    width: 100%;
    height: 80px;
    padding: 20px 0 10px;
    color: #fff;
    font-size: 16px;
    border-radius: 0;
    box-shadow: none;
    border: none;
    background: transparent;
    border-bottom: 1px solid #fff;
    appearance: none;
    -webkit-rtl-ordering: logical;
}