.std {
    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
        margin-bottom: 20px;
        text-transform: uppercase;
    }
    h1{
        font-size: 24px;
        line-height: 30px;
        @include mq("tablet", min) {
            font-size: 28px;
            line-height: 36px;
        }
    }
    h2{
        font-size: 20px;
        line-height: 26px;
        @include mq("tablet", min) {
            font-size: 24px;
            line-height: 30px;
        }
    }
    h3{
        margin-bottom: 10px;
        font-size: 16px;
        line-height: 22px;
        @include mq("tablet", min) {
            font-size: 18px;
            line-height: 24px;
        }
    }
    h4{
        font-size: 14px;
        line-height: 20px;
        @include mq("tablet", min) {
            font-size: 16px;
            line-height: 22px;
        }
    }
    p{
        margin-bottom: 20px;
    }
    img{
        margin: auto;
        display: inherit;
    }
    ul{
        display: block;
        list-style-type: disc;
        margin-block-start: 1em;
        margin-block-end: 1em;
        margin-inline-start: 0px;
        margin-inline-end: 0px;
        padding-inline-start: 40px;
        ul{
            list-style-type: circle;
        }
    }
    ul,ol{
        li{
            margin-bottom: 6px;
        }
    }
    a{
        &:hover{
            text-decoration: underline;
        }
    }
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: 'Montserrat', sans-serif;
    font-weight: 700;
    text-transform: uppercase;
    margin: 0;
    line-height: normal;
}

ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

a {
    text-decoration: none;
    color: $color-black;
    font-weight: 400;
    transition: color 0.2s ease;
    &:hover {
    }
}