.cms-contact-us{
    .contact-us{
        display: block;
        background-color: #000;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        position: relative;
        color: #fff;
        padding-top: 200px;
        padding-bottom: 60px;
        min-height: 100vh;
        @include mq('tablet', min) {
            padding-top: 300px;
            padding-bottom: 150px;
        }
        &:after{
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: rgba(0,0,0,.56);
            // background: rgb(0,0,0);
            // background: linear-gradient(0deg,rgba(0,0,0,.39),rgba(0,0,0,.8));
            transition: all .5s ease;
            z-index: 0;
        }
        > div{
            position: relative;
            z-index: 1;
            @include mq('tablet', min) {
                width: 100%;
            }
        }
        .content{
            max-width: 100%;
            text-align: left;
            @include mq('tablet') {
                margin-bottom: 60px;
            }
            @include mq('tablet', min) {
                margin-bottom: 60px;
            }
            .contain{
                max-width: inherit;
                @include mq('tablet', min) {
                    margin: 0 125px;
                }
            }
        }
    }
    .contactForm{
        max-width: 1020px;
        margin: 0 auto;
        .form{
            .fieldset{
                display: flex;
                flex-wrap: wrap;
                .field{
                    width: 100%;
                    padding: 0;
                    margin-bottom: 20px;
                    @include mq('tablet', min) {
                        width: 50%;
                        padding: 0 20px;
                        margin-bottom: 20px;
                    }
                    &.fullwidth{
                        width: 100%;
                    }
                }
                .control{
                    position: relative;
                    .input-text{
                        font-size: 16px;
                        &,
                        & + label {
                            position: relative;
                        }
                        &:focus,
                        &:not(:placeholder-shown) {
                            //border-color: rgba(white, .1);
                            & ~ label {
                                transform: translate(0, -16px) scale(.8);
                                opacity: .8;
                            }
                        }
                        & ~ label {
                            position: absolute;
                            top: 14px;
                            left: 0;
                            user-select: none;
                            pointer-events: none;
                            padding: 0;
                            font-size: 16px;
                            display: block;
                            transition: transform 0.25s, opacity 0.25s ease;                            
                            transform-origin: 0 0;
                            opacity: 1;
                        }
                        &::placeholder{
                            color: transparent;
                        }
                    }
                    
                }
            }
            .actions{
                display: flex;
                align-items: center;
                justify-content: center;
            }
        }
        .area-choices{
            padding: 0;
            margin-top: 20px;
            @include mq('tablet', min) {
                padding: 0 20px;
                margin-top: 30px;
            }
        }
        .request__title{
            color: #fff;
            font-size: 16px;
            margin-bottom: 40px;
        }
        .request__choices{
            position: relative;
            display: flex;
            flex-direction: row;
            flex-wrap: nowrap;
            justify-content: space-between;
            align-items: baseline;
            align-content: stretch;
            margin-bottom: 56px;
            color: #fff;
            @include mq('tablet') {
                display: grid;
                grid-template-rows: 70px 30px;
                grid-template-columns: 20px 20px 20px 20px;
                justify-content: space-between;
                align-content: space-evenly;
                margin-bottom: 24px;
                z-index: 0;
            }
            &:before{
                content: "";
                position: absolute;
                top: 50%;
                left: 20px;
                width: calc(100% - 40px);
                transform: translateY(-50%);
                height: 1px;
                background-color: hsla(0,0%,83.9%,.3);
                @include mq('tablet') {
                    top: 10px;
                    transform: translate(0);
                }
            }
            &:after{
                @include mq('tablet') {
                    content: "";
                    position: absolute;
                    bottom: 20px;
                    left: 20px;
                    width: calc(100% - 40px);
                    height: 1px;
                    background-color: hsla(0,0%,83.9%,.3);
                    z-index: 0;
                }
            }
            .item{
                display: inline-block;
                position: relative;
                padding: 9px;
                cursor: pointer;
                user-select: none;
                .label{
                    position: absolute;
                    top: -20px;
                    left: 50%;
                    font-style: normal;
                    font-weight: 500;
                    line-height: normal;
                    font-size: 9px;
                    text-align: center;
                    letter-spacing: .06em;
                    white-space: nowrap;
                    color: rgba($color: #fff, $alpha: .8);
                    transform: translateX(-50%);
                    transition: color .5s ease;
                }
                input{
                    position: absolute;
                    opacity: 0;
                    cursor: pointer;
                    outline: none;
                    
                }
                .checkmark{
                    position: absolute;
                    top: 0;
                    left: 0;
                    height: 18px;
                    width: 18px;
                    background-color: #f7f7f6;
                    border-radius: 50%;
                    -webkit-box-sizing: initial;
                    box-sizing: initial;
                    border: 1px solid transparent;
                    transition: all .5s ease;
                    outline: none;
                    &:before{
                        content: "";
                        position: absolute;
                        top: 50%;
                        left: 50%;
                        width: 4px;
                        height: 4px;
                        background-color: #afb0b2;
                        border-radius: 50%;
                        border: 1px solid #fff;
                        transform: translate(-50%,-50%);
                        transition: all 1s ease;
                    }
                }
                &:hover input ~ .checkmark,
                & input:checked ~ .checkmark {
                    -webkit-box-sizing: initial;
                    box-sizing: initial;
                    border: 1px solid #fff;
                    background-color: #1d1f26;
                    box-shadow: 0 2px 8px rgba($color: #000000, $alpha: .6);
                }
            }
        }
    }
}