@charset "UTF-8";

@font-face {
    font-family: 'montserratbold';
    src: url('../fonts/montserrat-bold-webfont.woff2') format('woff2'),
         url('../fonts/montserrat-bold-webfont.woff') format('woff');
    font-weight: 700;
    font-style: normal;

}

@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,700;1,700&amp;family=Quicksand:wght@300;400;500;600;700&amp;display=swap');

@import "_mixins";
@import "_breakpoints";
@import "_settings";
@import "components/variables";

@import "normalize.css";
@import "td-icon.css";
@import "animate.min.css";
@import "hamburgers.css";
@import "swiper-bundle.min.css";


*,
*:after,
*::before {
    box-sizing: border-box;
    outline: 0 none;
}

pre{
    font-family: 'Quicksand', sans-serif;
    font-size: 16px;
    @include mq("tablet", min) {
        font-size: 18px;
    }
}

@include mq("tablet-wide") {
    *::selection {
        background: transparent !important;
    }
    *{
        -webkit-tap-highlight-color: rgba(255,255,255,0);
        -webkit-tap-highlight-color: transparent;
    }
    a, button {
        user-select: none;
        // will-change: box-shadow,transform;
        // will-change: background-color,opacity;
    }
}
.on-mobile {
    display: none;
    @include mq("tablet") {
        display: block;
    }
}
.on-desktop {
    @include mq("tablet") {
        display: none;
    }
}
.no-display {
    display: none;
}
p {
    margin-top: 0; 
    margin-bottom: 20px;
}

p:empty {
    display: none;
    margin: 0;
    padding: 0;
    font-size: 0;
}

p:last-child {
    margin: 0;
}

img,
object,
embed,
video {
    max-width: 100%;
    height: initial;
}

h3.title{
    font-size: 28px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    margin-bottom: 20px;
    @include mq('tablet', min) {
        font-size: 30px;
    }
    @include mq('desktop-wide', min) {
        font-size: 36px;
    }
}

h4.title{
    font-size: 20px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    margin-bottom: 20px;
    @include mq('tablet', min) {
        font-size: 25px;
    }
}

html {
    text-rendering: optimizeLegibility !important;
    overflow-x: hidden;
    font-size: 100%;
}

html.mobile {
    overflow: hidden;
}

body {
    // font-family: 'Montserrat', sans-serif;
    font-family: 'Quicksand', sans-serif;
    font-weight: 400;
    color: $color-black;
    font-size: 16px;
    line-height: 1.4;
    overflow-x: hidden;
    background-color: $color-white;
    margin: 0;
    padding: 0;
    @include mq("tablet", min) {
        font-size: 18px;
    }
}

.about{
    display: flex;
    flex-wrap: wrap;
    > div{
        @include mq('tablet', min) {
            width: 50%;
        }
    }
    .content{ 
        .contain{
            @include mq('tablet', min) {
                padding-bottom: 15%;
            }
        }
    }
    .about-image{
        @include mq('tablet') {
            order: 1;
        }
    }
    .about-image-multi{
        position: relative;
        @include mq('tablet') {
            margin-bottom: 25px;
        }
        @include mq('desktop-wide', min) {
            margin-top: -10%;
        }
        .img-pos01{

        }
        .img-pos02{
            img{
                position: absolute;
                top: -15%;
                right: 20%;
            }
        }
    }
    .about-description{
        max-width: 400px;
        text-align: justify;
        @include mq('tablet') {
            margin-bottom: 25px;
        }
        @include mq('tablet', min) {
            margin-top: 80px;
            padding-right: 40px;
        }
    }
}

.project{
    background-color: #000;
    .container{
        display: flex;
        flex-wrap: wrap;
        @include mq('tablet', min) {
            // margin-left: -10px;
            // margin-right: -10px;
        }
    }
    .content{
        justify-content: end;
        text-align: right;
        color: $color-white;
        @include mq('tablet') {
            order: -1;
            width: 100%;
        }
        .contain{
            max-width: 600px;
            @include mq('tablet', min) {
                padding-left: 40px;
                padding-right: 40px;
            }
            @include mq('desktop-wide', min) {
                padding-right: 125px;
            }
        }
    }
    .project-image{
        @include mq('tablet', min) {
            width: 100%;
        }
        img{
            width: 100%;
        }
    }
    .two-col{
        @include mq('tablet', min) {
            width: 50%;
        }
    }
    .more{
        color: $color-white;
        &:before,
        &:after{
            background-color: $color-white;
        }
    }
}

.news,
.careers{
    padding: 40px 20px;
    display: flex;
    font-size: 16px;
    @include mq('tablet') {
        flex-wrap: wrap;
    }
    @include mq('tablet', min) {
        padding: 150px 20px 80px;
    }
    @include mq('tablet-wide', min) {
        padding: 150px 40px 80px;
    }
    @include mq('desktop', min) {
        padding: 150px 60px 80px;
    }
    @include mq('desktop-wide', min) {
        padding: 150px 130px 80px;
        font-size: 18px;
    }
    > div{
        @include mq('tablet', min) {
            width: 50%;
        }
    }
}
.news + .careers{
    padding-top: 0;
}
.careers{
    padding-bottom: 40px;
    @include mq('tablet', min) {
        padding-bottom: 80px;
    }
    .content{
        justify-content: end;
        text-align: right;
        @include mq('tablet') {
            order: -1;
        }
        .contain{
            @include mq('tablet', min) {
                padding-left: 0;
                padding-right: 40px;
            }
            @include mq('desktop-wide', min) {
                padding-right: 125px;
            }
        }
    }
}

.contact{
    padding: 80px 20px;
    // min-height: 768px;
    display: flex;
    flex-wrap: wrap;
    background-color: #D9D9D9;
    font-size: 16px;
    @include mq('tablet', min) {
        padding: 100px 20px 150px;
        font-size: 18px;
    }
    @include mq('desktop-wide', min) {
        padding: 150px 130px 150px;
    }
    > div{
        @include mq('tablet', min) {
            width: 50%;
        }
    }
    .content{
        display: flex;
        
        .contain{
            max-width: 500px;
            @include mq('tablet', min) {
                padding-left: 40px;
            }
            @include mq('desktop-wide', min) {
                padding-left: 125px;
            }
        }
    }
}