.cms-project{
    background-color: #000;
    color: #fff;
    .page-main{
        .container{
            max-width: inherit;
        }
    }
    .project-page.project{
        display: block;
        position: relative;
        padding-top: 200px;
        padding-bottom: 60px;
        @include mq('tablet', min) {
            padding-top: 300px;
            padding-bottom: 150px;
        }
        > div{
            position: relative;
            z-index: 1;
        }
    }
    .project-main{
        display: flex;
        flex-wrap: wrap;
        padding: 0 0 40px;
        @include mq('tablet', min) {
            flex-direction: row-reverse;
            padding: 0 10px 40px;
        }
        > div{
            @include mq('tablet', min) {
                width: 50%;
            }
        }
        .project-title{
            @include mq('tablet', min) {
                text-align: right;
                padding-right: 40px;
            }
            @include mq('desktop-wide', min) {
                padding-right: 125px;
            }
        }
        .project-content{
            text-align: justify;
        }
    }
    .project-tabs{
        padding: 0 10px 40px;
        width: 100%;
        @include mq('desktop-wide', min) {
            max-width: 50%;
            padding: 0 10px 40px;
        }
        >ul{
            display: flex;
            width: 100%;
            @include mq('tablet') {
                justify-content: space-around;
            }
            @include mq('tablet', min) {
                justify-content: space-between;
                max-width: 50%;
                margin: 0 auto;
            }
            li{
                &.active{
                    a{
                        pointer-events: none;
                        text-decoration: underline;
                    }
                }
            }
            a{
                font-size: 16px;
                color: #fff;
                &:hover{
                    text-decoration: underline;
                }
            }
        }
    }
    .product-block{
        display: flex;
        flex-wrap: wrap;
    }
}

.project-linkmore{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 60px 0 0;
    width: 100%;
}

.project-image{
    margin-bottom: 25px;
    @include mq('tablet', min) {
        padding: 14px 10px;
        margin: 0;
    }
    > a{
        display: block;
        position: relative;
        color: #fff;
        img{
            z-index: -1;
            @include mq('tablet') {
                height: 300px;
                object-fit: cover;
            }
        }
        &:after{
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: rgb(0,0,0);
            background: linear-gradient(0deg,rgba(0,0,0,.63) 0,rgba(0,0,0,.24));
            z-index: 0;
            transition: all .5s ease;
            @include mq('tablet', min) {
                opacity: 0;
            }
        }
        &:hover{
            &:after{
                opacity: 1;
            }
            .project-detail,
            .project-link{
                opacity: 1;
            }
        }
    }
    .project-detail,
    .project-link{
        position: absolute;
        left: 20px;
        transition: opacity .5s ease;
        z-index: 1;
        @include mq('tablet', min) {
            left: 25px;
            opacity: 0;
        }
    }
    .project-detail{
        font-size: 14px;
        top: 16px;
        @include mq('tablet', min) {
            font-size: 16px;
        }
        span{
            display: inline-block;
            margin-right: 25px;
            @include mq('tablet', min) {
                margin-right: 60px;
            }
        }
    }
    .project-link{
        font-family: Montserrat;
        font-size: 16px;
        font-weight: 700;
        line-height: 28px;
        bottom: 20px;
      text-transform: uppercase;
        @include mq('tablet', min) {
            font-size: 20px;
        }
    }
}

.project-detail-page{
    padding-top: 200px;
    padding-bottom: 60px;
    @include mq('tablet', min) {
        padding-top: 300px;
        padding-bottom: 150px;
    }
    & + .project.block{
        padding-top: 0;
        .title{
            text-align: center;
        }
    }
}
.project-main-content{
    display: flex;
    flex-wrap: wrap;
    .project-image{
        padding: 0;
        @include mq('tablet', min) {
            width: 50%;
        }
        > a{
            pointer-events: none;
            &:after{
                opacity: 1;
            }
            .project-detail,
            .project-link{
                opacity: 1;
            }
        }
    }
    .project-info{
        display: flex;
        flex-flow: wrap column;
        align-items: flex-end;
        @include mq('tablet', min) {
            width: 50%;
        }
        .content{
            
        }
        .contain{
            max-width: 500px;
            display: flex;
            flex-flow: wrap column;
            align-items: flex-end;
            @include mq('tablet', min) {
                padding-left: 40px;
                padding-right: 40px;
            }
            @include mq('desktop-wide', min) {
                padding-right: 125px;
            }
            .title{
                text-align: right;
            }
            p{
                text-align: right;
            }
        }
    }
}

.project-desscription,
.project-gallery{
    margin-top: 100px;
}