.cms-index{
    .about.block{
        @include mq('tablet', min) {
            padding-right: 0;
        }
        > div{
            width: 100%;
            @include mq('tablet', min) {
                width: 60%;
            }
        }
        .content{
            @include mq('tablet', min) {
                width: 50%;
            }
            @include mq('desktop', min) {
                width: 62%;
            }
        }
        .about-image,
        .about-description{
            @include mq('tablet', min) {
                width: 50%;
            }
            @include mq('desktop', min) {
                width: 38%;
            }
        }
        .about-image-multi{
            display: flex;
            @include mq('tablet') {
                padding-top: 30px;
            }
            @include mq('tablet', min) {
                width: 50%;
                padding-left: 40px;
                padding-right: 40px;
            }
            @include mq('desktop', min) {
                width: 62%;
            }
            @include mq('desktop-wide', min) {
                padding-left: 125px;
            }
            .img-pos02{
                width: 198px;
                position: relative;
            }
        }
    }
    .project.block{
        .container{
            max-width: inherit;
        }
        .content{
            .contain{
                display: flex;
                flex-direction: column;
                align-items: flex-end;
                @include mq('tablet', min) {
                    padding-left: 40px;
                }
            }
        }
    }
    .news.block{
        > div{
            width: initial;
        }
        .content{
            flex-grow: 2;
            @include mq('tablet', min) {
                padding-right: 40px;
            }
        }
        .image{
            
        }
    }
    .careers.block{
        display: flex;
        @include mq('tablet', min) {
            padding-left: 0;
        }
        > div{
            width: initial;
        }
        .content{
            flex-grow: 2;
            @include mq('tablet', min) {
                // padding-left: 40px;
            }
        }
        .careers-image-multi{
            display: flex;
            @include mq('tablet', min) {
                // width: 44%;
            }
            .img-pos02{
                padding-left: 40px;
            }
        }
    }
}