.cms-careers{
    .careers-page{
        display: block;
        position: relative;
        padding-top: 200px;
        padding-bottom: 60px;
        padding-left: 20px;
        padding-right: 20px;
        @include mq('tablet', min) {
            padding-top: 300px;
            padding-bottom: 150px;
        }
        .content{
            max-width: 100%;
            text-align: left;
            @include mq('tablet') {
                margin-bottom: 60px;
            }
            @include mq('tablet', min) {
                margin-bottom: 100px;
            }
            .contain{
                max-width: inherit;
                @include mq('tablet', min) {
                    margin: 0 125px;
                }
            }
        }
        // .more{
        //     color: #fff;
        //     &:before,
        //     &:after{
        //         background-color: #fff;
        //     }
        // }
    }
    .project-linkmore{
        
    }
}

.career-main{
    .items{
        display: flex;
        flex-wrap: wrap;
        // margin: 0 -50px;
        @include mq('desktop-wide', min) {
            margin: 0 -50px;
        }
        .item{
            width: percentage($number: 1/3);
            padding: 0 50px;
            margin-bottom: 60px;
            &:nth-child(n+4){
                display: none;
            }
            @include mq('tablet', min) {

            }
          > a{
            display: block;
          }
        }
      .caption{
        margin-top: 20px;
      }
      .subscription{
        display: block;
        display: -webkit-box;
        max-width: 100%;
        -webkit-line-clamp: 3;
        -webkit-box-orient: vertical;
        overflow: hidden;
        text-overflow: ellipsis;
        margin-bottom: 20px;
      }
    }
}



