.careers-detail-page{
    padding-top: 200px;
    padding-bottom: 60px;
    padding-left: 20px;
    padding-right: 20px;
    @include mq('tablet', min) {
        padding-top: 300px;
        padding-bottom: 150px;
    }
    .container{
        flex-direction: column;
    }
    .heading-block{
        text-align: right;
        margin-bottom: 60px;
    }
