.cms-about-us{
    background-color: #fff;
    .about-page{
        position: relative;
        // color: #fff;
        padding-top: 200px;
        padding-bottom: 60px;
        display: flex;
        flex-wrap: wrap;
        @include mq('tablet', min) {
            padding-top: 240px;
        }
        .content{
            flex-grow: 2;
            @include mq('tablet', min) {
                width: 50%;
                padding-right: 40px;
            }
            &.ltr{
                @include mq('tablet', min) {
                    padding-left: 40px;
                }
                .contain{
                    max-width: 500px;
                    padding-bottom: 0;
                }
            }
        }
        .about-image{
            @include mq('tablet') {
                order: 1;
            }
            @include mq('tablet', min) {
                width: 50%;
            }
        }
        .about-image-multi {
            display: flex;
            margin-top: 70px;
            @include mq('tablet') {
                margin-bottom: 25px;
            }
            @include mq('tablet', min) {
                width: 50%;
                margin-top: 200px;
            }
            .img-pos01{
                img{
                    position: relative;
                    top: -70px;
                    left: 24px;
                    @include mq('tablet', min) {
                        top: -90px;
                    left: 64px;
                    }
                }
            }
            .img-pos02{
                position: relative;
                z-index: 1;
            }
        }
    }
    .about-waypoint{
        display: flex;
        flex-wrap: wrap;
        
        @include mq('tablet', min) {
            padding-right: 0;
        }
        > div{
            @include mq('tablet', min) {
                width: 50%;
            }
        }
        .waypoint-block{
            @include mq('tablet') {
                width: 100%;
                order: -1;
                margin-bottom: 30px;
            }
            .content{
                display: flex;
                flex-wrap: wrap;
                align-items: flex-start;
                max-width: 320px;
                @include mq('tablet') {
                    justify-content: center;
                    margin: 0 auto;
                }
                @include mq('tablet', min) {
                    margin: 100px 50px;
                    max-width: 360px;
                }
                @include mq('desktop-wide', min) {
                    
                }
                .box{
                    width: 50%;
                    margin-bottom: 30px;
                    @include mq('tablet', min) {
                        // width: 180px;
                        margin-bottom: 40px;
                    }
                }
                .number{
                    font-family: 'Montserrat', sans-serif;
                    font-weight: 700;
                    font-size: 44px;
                    line-height: normal;
                    @include mq('tablet', min) {
                        font-size: 50px;
                    }
                    @include mq('desktop-wide', min) {
                        font-size: 60px;
                    }
                }
                .text{
                    color: #707070;
                    font-size: 16px;
                    max-width: 120px;
                    @include mq('tablet', min) {
                        font-size: 18px;
                        max-width: 150px;
                    }
                    @include mq('desktop-wide', min) {
                        font-size: 20px;
                    }
                }
            }
        }
        .about-image.pos{
            @include mq('tablet') {
                display: none;
            }
            @include mq('tablet', min) {
                position: absolute;
                right: 0;
                bottom: -40px;
                width: 25%;
            }
            @include mq('desktop-wide', min) {
                width: inherit;
                bottom: 0;
            }
        }
    }
}

.team-profile{
    padding-top: 0;
    .content{
        padding-bottom: 0;
        @include mq('tablet', min) {
            padding-bottom: 120px;
        }
    }
    .caption{
        padding-top: 20px;
        > div{
            font-size: 14px;
            @include mq('tablet', min) {
                font-size: 16px;
            }
        }
        a{
            &:hover{
                text-decoration: underline;
            }
        }
        .ceo{
            font-family: 'Montserrat', sans-serif;
            font-size: 20px;
            font-weight: 700;
            @include mq('tablet', min) {
                font-size: 25px;
            }
        }
        .name{
            font-family: 'Montserrat', sans-serif;
            font-size: 16px;
            margin-top: 10px;
            @include mq('tablet', min) {
                font-size: 18px;
            }
        }
        .level{
            font-size: 16px;
            @include mq('tablet', min) {
                font-size: 18px;
            }
        }
        .email{
            a{
                color: #004040;
            }
        }
        .phone{
            a{
                color: #000;
            }
        }
    }
    .folio-slide{
        position: relative;
        @include mq('tablet', min) {
            padding: 0 40px;
        }
        @include mq('desktop-wide', min) {
            padding: 0;
        }
        img{
            display: block;
            width: 100%;
        }
        .swiper{
            @include mq('tablet') {
                overflow: visible;
                padding-right: 100px;
            }
        }
        .swiper-button{
            top: 32%;
            @include mq('tablet') {
                display: none;
            }
            &.swiper-button-prev-custom{
                left: -60px;
            }
            &.swiper-button-next-custom{
                right: -60px;
            }
        }
    }
}